<template>
    <div class="add-message">
        <form class="add-from form-horizontal">
            <div class="form-group">
                <label for="inputEmail3" class="col-sm-1 control-label">通知标题</label>
                <div class="col-sm-11">
                    <input type="text" class="form-control" id="inputEmail3" placeholder="通知标题" v-model="title">
                </div>
            </div>
            <div class="form-group">
                <label for="inputPassword3" class="col-sm-1 control-label">通知内容</label>
                <div class="col-sm-11">
                   <textarea class="form-control" rows="3" placeholder="通知内容" v-model="content"></textarea>
                </div>
            </div>
            <div class="form-group">
                <div class="col-sm-offset-1 col-sm-11">
                    <button type="submit" class="addbtn btn btn-success" @click="addMessage">完成</button>
                </div>
            </div>
        </form>
    </div>
</template>
<script>
export default {
  data() {
    return {
      title: "",
      content: ""
    };
  },
  methods: {
    addMessage() {
      $.ajax({
        type: "POST",
        url: window.url + "/admin/message",
        data: {
          title: this.title,
          content: this.content
        },
        success: data => {
          if (data.success) {
           this.$router.push('/message')
          }
        }
      });
    }
  }
};
</script>

<style>
.add-from {
  padding: 60px 60px;
}
.addbtn {
  padding-left: 30px;
  padding-right: 30px;
}
@media (max-width: 768px) {
  .add-from {
    padding: 20px 20px;
  }
  .addbtn {
    width: 100%;
  }
}
</style>





