<template>
    <div class="message">
        <div class="wrap-head">
            <button type="button" class="btn btn-success" @click="addTime">添加闭馆信息</button>
        </div>
        <div class="message-wrap row">
            <div class="col-md-12">
                <div class="panel panel-default">
                    <div class="table-responsive">
                        <table class="all-table table table-hover table-bordered">
                            <thead>
                                <tr>
                                    <th>#</th>
                                    <th>闭馆日期</th>
                                    <th>闭馆时间</th>
                                    <th>结束日期</th>
                                    <th>结束时间</th>
                                    <th>闭馆原因</th>
                                    <th>操作</th>
                                 </tr>
                            </thead>
                            <tbody>
                                <tr v-for="(item,index) in messageList" :key="item.id">
                                    <td>{{index+1}}</td>
                                    <td>{{item.startDate|date}}</td>
                                    <td v-if="item.startTime !== null">{{item.startTime|timepicker}}</td>
                                    <td v-else>整天</td>
                                    <td>{{item.endDate|date}}</td>
                                    <td v-if="item.endTime !== null">{{item.endTime|timepicker}}</td>
                                    <td v-else>整天</td>
                                    <td>{{item.reason}}</td>
                                    <td>
                                        <button type="button" class="btn btn-danger"  @click="deleteMessage(item,index)">删除</button>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        </div>
        <pagination :total="timeTotal" @pagination="pagination" v-if="timeTotal > 10"></pagination>
          <div class="modal" id="adddTimeModal" tabindex="-1" role="dialog"  aria-labelledby="exampleModalCenterTitle" aria-hidden="true">
            <div class="modal-dialog modal-dialog-centered" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">添加闭馆信息</h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                            </button>
                    </div>
                    <div class="modal-body">
                        <div class="input-group" style="width:100%;padding:8px 0px">
                              <el-date-picker
                                    v-model="closeStartDate"
                                    type="date"
                                    placeholder="闭馆开始日期"
                                    style="width:100%">
                              </el-date-picker>
                        </div>
                         <div class="input-group" style="width:100%;padding:8px 0px">
                             <el-time-picker
                                v-model="closeStartTime"
                                value-format="HH:mm" format="HH:mm"
                                placeholder="闭馆开始时间"
                                style="width:100%">
                            </el-time-picker>
                        </div>
                        <div class="input-group" style="width:100%;padding:8px 0px">
                              <el-date-picker
                                    v-model="closeEndDate"
                                    type="date"
                                    placeholder="闭馆结束日期"
                                    style="width:100%">
                              </el-date-picker>
                        </div>
                        <div class="input-group" style="width:100%;padding:8px 0px">
                             <el-time-picker
                                v-model="closeEndTime"
                                placeholder="闭馆结束时间"
                                value-format="HH:mm" format="HH:mm"
                                style="width:100%">
                            </el-time-picker>
                        </div>
                        <div class="input-group" style="width:100%;padding:8px 0px">
                              <el-input type="textarea" v-model="closeReason" placeholder="闭馆原因"></el-input>
                        </div>
                    </div>
                    <div class="modal-footer"> 
                        <button type="button" class="btn btn-primary" @click="confirmAdd">确定</button>
                        <button type="button" class="btn btn-secondary" data-dismiss="modal">取消</button>
                    </div>
                </div>
            </div>
        </div>
        <div class="modal" id="exampleModal" tabindex="-1" role="dialog"  aria-labelledby="exampleModalCenterTitle" aria-hidden="true">
            <div class="modal-dialog modal-dialog-centered" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">删除</h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                            </button>
                    </div>
                    <div class="modal-body">
                        <p>确认删除该条信息?</p>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-primary" @click="confirmDelete">确定</button>
                        <button type="button" class="btn btn-secondary" data-dismiss="modal">取消</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import moment from "moment";
import Pagination from "../public/pagination";

export default {
  data() {
    return {
      messageList: [],
      closeTimeId: 0,
      index: 0,
      timeTotal: 0,
      closeStartDate: "",
      closeStartTime: "",
      closeEndDate: "",
      closeEndTime: "",
      closeReason: "" // 闭馆原因
    };
  },
  components: {
    Pagination
  },
  mounted() {
    this.getCloseTime()
  },
  methods: {
    // 获取闭馆信息
    getCloseTime() {
      $.ajax({
        type: "GET",
        url: window.url + "/closeinfo/all",
        success: data => {
          if (data.success) {
            this.messageList = data.result.data;
            this.timeTotal = data.result.total;
          }
        }
      });
    },
     // 分页
    pagination(page) {
      $.ajax({
        type: "GET",
        url: window.url + "/closeinfo/all?page=" + page,
        success: data => {
          if (data.success) {
            this.messageList = data.result.data;
            this.timeTotal = data.result.total;
          }
        }
      });
    },
    // 增加消息
    addTime() {
      $("#adddTimeModal").modal("show");
    },
    // 删除消息
    deleteMessage(item, index) {
      $("#exampleModal").modal("show");
      this.index = index;
      this.closeTimeId = item.id;
    },
    confirmDelete() {
      $.ajax({
        type: "DELETE",
        url: window.url + "/closeinfo/" + this.closeTimeId,
        success: data => {
          if (data.success) {
            $("#exampleModal").modal("hide");
            this.messageList.splice(this.index, 1);
            this.$alert("删除成功", "提示", {
              confirmButtonText: "确定",
               callback: action => {
                   this.getCloseTime()
               }
            });
          }
        }
      });
    },
    confirmAdd() {
      $.ajax({
        type: "PUT",
        url: window.url + "/closeinfo/add",
        data: {
          startDate: moment(this.closeStartDate).format("YYYY-MM-DD"),
          startTime: this.closeStartTime,
          endDate: moment(this.closeEndDate).format("YYYY-MM-DD"),
          endTime: this.closeEndTime,
          reason: this.closeReason
        },
        success: data => {
          if (data.success) {
            console.log(data);
            $("#exampleModal").modal("hide");
            $("#adddTimeModal").modal("hide");
            this.getCloseTime()
            // this.messageList.splice(this.index, 1);
          }
        }
      });
    }
  }
};
</script>

<style>
.wrap-head {
  height: 80px;

  border-bottom: 1px solid #ccc;

  width: 100%;

  padding: 20px;
}

.message-wrap {
  width: 100%;

  padding: 40px 0px;

  margin-right: 0px;

  margin-left: 0px;
}
</style>




