<template>
    <div class="login" style="background-color: #E2E2E2;">
        <div class="login-warp container">
            <div class="row text-center " style="padding-top:100px;">
                <div class="col-md-12">
                       <h3>小球馆后台管理系统</h3>
                </div>
            </div>
            <div class="row ">
                <div class="col-md-4 col-md-offset-4 col-sm-6 col-sm-offset-3 col-xs-10 col-xs-offset-1">
                    <div class="panel-body">
                        <div class="form-group input-group">
                            <span class="input-group-addon"><i class="fa fa-tag" ></i></span>
                            <input type="text" class="form-control" placeholder="用户名" v-model="username"/>
                        </div>
                        <div class="form-group input-group">
                            <span class="input-group-addon"><i class="fa fa-lock"  ></i></span>
                            <input type="password" class="form-control" placeholder="密码" v-model="password"/>
                        </div>
                        <div class="login-btn btn btn-success" @click="login">登  录</div>
                        <div class="warn-alert alert alert-warning alert-dismissible" role="alert" v-show="errorTip">
                             <button type="button" class="warn-alert-close close" data-dismiss="alert" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                            {{errorMsg}}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
  data() {
    return {
      username: "",
      password: "",
      errorMsg: "",
      errorTip: false
    };
  },
  methods: {
    login(name) {
      if (this.username === "admin") {
        $.ajax({
          type: "POST",
          url: window.url + "/root/login",
          data: {
            username: this.username,
            password: this.password
          },
          // xhrFields: {
          //   withCredentials: true
          // },
          success: data => {
            if (data.success) {
              window.localStorage.setItem("name", this.username);
              this.$router.push("/message");
            } else {
              this.errorMsg = data.msg;
              this.errorTip = true;
            }
          }
        });
      } else {
        window.localStorage.setItem("name", this.username);
        this.$router.push("/message");
      }
    }
  }
};
</script>

<style>
.warn-alert-close {
  right: 5px !important;
}
.warn-alert {
  top: 10px;
  position: relative;
  padding: 10px 0px;
  text-align: center;
}
.login {
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: url("/static/img/bg.jpg");
  background-size: 100% 100%;
  background-color: rgba(2, 2, 2, 0.6);
}
.login-btn {
  width: 100%;
}
.login-warp {
  position: relative;
  top: 20%;
}
</style>




