<template>
 <div>
     <nav class="navbar navbar-default navbar-cls-top " role="navigation" style="margin-bottom: 0">
            <div class="navbar-header">
                <button type="button" class="navbar-toggle" data-toggle="collapse" data-target=".sidebar-collapse">
                    <span class="sr-only">Toggle navigation</span>
                    <span class="icon-bar"></span>
                    <span class="icon-bar"></span>
                    <span class="icon-bar"></span>
                </button>
                <a class="navbar-brand" href="#">小球馆管理系统</a>
            </div>
            <div class="header-right">
            </div>
    </nav>
   <nav class="navbar-default navbar-side" role="navigation" style="top: 0px;bottom: 0px;">
            <div class="sidebar-collapse">
                <ul class="nav" id="main-menu">
                    <li>
                        <div class="user-img-div">
                            <img src="../../assets/img/user.png" class="img-thumbnail" />
                            <div class="inner-text">
                               当前登录用户：{{username}}
                            <br />
                                <small @click="logout" class="logout-style">退出登录</small>
                            </div>
                        </div>

                    </li>
                    <li>
                       <router-link to="/totalMessage"><i class="fa fa-desktop "></i>统计信息</router-link>
                    </li>
                    <li>
                       <router-link to="/message"><i class="fa fa-desktop "></i>通知管理</router-link>
                    </li>
                     <li  @click="displayList">
                        <a href="javascript:void(0)"><i class="fa fa-life-bouy "></i>订单管理
                        <span class="fa arrow" v-if="!showlist"></span>
                        <span class="fa fa-angle-down" v-else style="float:right"></span></a>
                         <ul class="nav nav-second-level" v-show="showlist">
                             <li @click.stop="routerPush('/order')">
                                <a href="javascript:void(0)"><i class="fa fa-life-bouy"></i>未支付订单</a>
                            </li>
                            <li  @click.stop="routerPush('/historyOrder')">
                               <a href="javascript:void(0)"><i class="fa fa-coffee"></i>历史订单</a>
                            </li>
                             <li @click.stop="routerPush('/gymnastics')">
                               <a href="javascript:void(0)"><i class="fa fa-coffee"></i>体操室管理</a>
                            </li>
                        </ul>
                    </li>
                    <li>
                        <router-link to="/closeTime"><i class="fa fa-reorder "></i>闭馆时间管理</router-link>
                    </li>
                     <li v-if="username==='admin'">
                        <router-link to="/users"><i class="fa fa-user"></i>管理员管理</router-link>
                    </li>
                </ul>
            </div>
        </nav>
    </div>
</template>
<script>
export default {
//   props: {
//     showlist:Boolean
//   },
  data() {
    return {
      username: "",
      showlist: true
    };
  },
  mounted() {
    this.username = window.localStorage.getItem("name");
  },
  methods: {
    displayList() {
      if (this.showlist === false) {
        this.showlist = true;
      } else {
        this.showlist = false;
      }
    },
    routerPush(str) {
      console.log(str);
      this.showlist = true;
      this.$router.push(str);
     
    },
    logout() {
      this.$router.push("/");
      //   $.ajax({
      //     type: "POST",
      //     url: window.url + "/logout",
      //     success: data => {
      //       if (data.success) {
      //         this.$router.push("/");
      //       }
      //     }
      //   });
    }
  }
};
</script>
<style>
.logout-style {
  cursor: pointer;
}
.logout-style:hover {
  color: #cccccc;
}
.fa.arrow:before {
  content: "\f104";
}
.fa-angle-down:before {
  content: "\f107";
}
</style>





