<template>
    <div class="message">
        <div class="wrap-head">
            <button type="button" class="btn btn-success" @click="addMessage">添加通知</button>
        </div>
        <div class="message-wrap row">
            <div class="col-md-12">
                <div class="panel panel-default">
                    <div class="table-responsive">
                        <table class="all-table table table-hover table-bordered">
                            <thead>
                                <tr>
                                    <th>#</th>
                                    <th>通知标题</th>
                                    <th>通知内容</th>
                                    <th>通知时间</th>
                                    <th>操作</th>
                                 </tr>
                            </thead>
                            <tbody>
                                <tr v-for="(item,index) in messageList" :key="item.id">
                                    <td class="td-index">{{index+1}}</td>
                                    <td class="td-title">{{item.title}}</td>
                                    <td class="td-content">{{item.content}}</td>
                                    <td class="td-time">{{item.ctime|time}}</td>
                                    <td class="td-options">
                                        <button type="button" class="btn btn-danger"  @click="deleteMessage(item,index)">删除</button>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        </div>
        <pagination :total="total" @pagination="pagination" v-if="total > 10"></pagination>
        <div class="modal" id="exampleModal" tabindex="-1" role="dialog"  aria-labelledby="exampleModalCenterTitle" aria-hidden="true">
            <div class="modal-dialog modal-dialog-centered" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">删除</h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                            </button>
                    </div>
                    <div class="modal-body">
                        <p>确认删除 "{{messagesName}}" 消息?</p>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-primary" @click="confirmDelete">确定</button>
                        <button type="button" class="btn btn-secondary" data-dismiss="modal">取消</button>
                    </div>
                </div>
            </div>
        </div>
        <!-- 操作提示 -->
        <div class="modal" id="successModal" tabindex="-1" role="dialog"  aria-labelledby="exampleModalCenterTitle" aria-hidden="true">
            <div class="modal-dialog modal-dialog-centered" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">提示</h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                            </button>
                    </div>
                    <div class="modal-body">
                        <p>{{optionTip}}</p>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-primary" @click="confirmOption">确定</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import Pagination from "../public/pagination";
export default {
  data() {
    return {
      messageList: [],
      messagesName: "",
      messageId: 0,
      index: 0,
      optionTip: "操作成功",
      total: 0
    };
  },
  components: {
    Pagination
  },
  mounted() {
      this.getAllMessage()
  },
  methods: {
    // 获取所有消息
    getAllMessage() {
      $.ajax({
        type: "GET",
        url: window.url + "/message/messages",
        success: data => {
          if (data.success) {
            this.messageList = data.result.data;
            this.total = data.result.total;
          }
        }
      });
    },
    // 分页
    pagination(page) {
      $.ajax({
        type: "GET",
        url: window.url + "/message/messages?page=" + page,
        success: data => {
          if (data.success) {
            this.messageList = data.result.data;
            this.total = data.result.total;
          }
        }
      });
    },
    // 获取消息
    getMessage() {},
    // 增加消息
    addMessage() {
      this.$router.push("/add_message");
    },
    // 删除消息
    deleteMessage(item, index) {
      this.messagesName = item.title;
      $("#exampleModal").modal("show");
      this.index = index;
      this.messageId = item.id;
    },
    confirmDelete() {
      $.ajax({
        type: "DELETE",
        url: window.url + "/admin/message/" + this.messageId,
        success: data => {
          if (data.success) {
            $("#exampleModal").modal("hide");
            this.messageList.splice(this.index, 1);
            $("#successModal").modal("show");
          } else {
            this.optionTip = data.msg;
            $("#successModal").modal("show");
          }
        }
      });
    },
    // 确认提示信息
    confirmOption() {
      $("#successModal").modal("hide");
      this.getAllMessage()
    }
  }
};
</script>

<style>
.wrap-head {
  height: 80px;

  border-bottom: 1px solid #ccc;

  width: 100%;

  padding: 20px;
}

.message-wrap {
  width: 100%;

  padding: 40px 0px;

  margin-right: 0px;

  margin-left: 0px;
}
.td-index{
    min-width: 40px;
}
.td-title{
    min-width: 80px;
}
.td-time{
    min-width: 140px;
}
.td-options{
    min-width: 60px;
}
</style>




