<template>
    <div class="message">
        <div class="wrap-head">
            <button type="button" class="btn btn-success" @click="addTime">预订体操室</button>
        </div>
        <div class="message-wrap row">
            <div class="col-md-12">
                <div class="panel panel-default">
                    <div class="table-responsive">
                        <table class="all-table table table-hover table-bordered">
                            <thead>
                                <tr>
                                    <th>#</th>
                                    <th>预订日期</th>
                                    <th>预订时间段</th>
                                    <th>操作</th>
                                 </tr>
                            </thead>
                            <tbody>
                                <tr v-for="(item,index) in messageList" :key="item.id">
                                    <td>{{index+1}}</td>
                                    <td>{{item.startDate}}</td>
                                    <td v-if="item.type===0">课余时间</td>
                                    <td v-else>晚自习时间</td>
                                    <td>
                                        <button type="button" class="btn btn-danger"  @click="deleteMessage(item,index)">取消</button>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        </div>
        <pagination :total="timeTotal" @pagination="pagination" v-if="timeTotal > 10"></pagination>
          <div class="modal" id="adddTimeModal" tabindex="-1" role="dialog"  aria-labelledby="exampleModalCenterTitle" aria-hidden="true">
            <div class="modal-dialog modal-dialog-centered" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">预订体操室</h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                            </button>
                    </div>
                    <div class="modal-body">
                        <div class="input-group" style="width:100%;padding:8px 0px">
                              <el-date-picker
                                    v-model="orderDate"
                                    type="date"
                                    placeholder="预订日期"
                                    style="width:100%">
                              </el-date-picker>
                        </div>
                        <div class="input-group" style="width:100%;padding:8px 0px">
                            <el-radio v-model="radio" label="0">课余时间</el-radio>
                            <el-radio v-model="radio" label="1">晚自习时间</el-radio>
                        </div>
                    </div>
                    <div class="modal-footer"> 
                        <button type="button" class="btn btn-primary" @click="confirmAdd">确定</button>
                        <button type="button" class="btn btn-secondary" data-dismiss="modal">取消</button>
                    </div>
                </div>
            </div>
        </div>
        <div class="modal" id="exampleModal" tabindex="-1" role="dialog"  aria-labelledby="exampleModalCenterTitle" aria-hidden="true">
            <div class="modal-dialog modal-dialog-centered" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">删除</h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                            </button>
                    </div>
                    <div class="modal-body">
                        <p>确认取消该预订?</p>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-primary" @click="confirmDelete">确定</button>
                        <button type="button" class="btn btn-secondary" data-dismiss="modal">取消</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import moment from "moment";
import Pagination from "../public/pagination";

export default {
  data() {
    return {
      radio:'0',
      messageList: [],
      closeTimeId: 0,
      index: 0,
      timeTotal: 0,
      orderDate: "",
    };
  },
  components: {
    Pagination
  },
  mounted() {
    this.getGymnastics()
  },
  methods: {
    // 获取闭馆信息
    getGymnastics() {
      $.ajax({
        type: "GET",
        url: window.url + "/admin/gymnastics",
        success: data => {
          if (data.success) {
              console.log(data.result.data)
            this.messageList = data.result.data;
            this.timeTotal = data.result.total;
          }
        }
      });
    },
     // 分页
    pagination(page) {
      $.ajax({
        type: "GET",
        url: window.url + "/closeinfo/all?page=" + page,
        success: data => {
          if (data.success) {
            this.messageList = data.result.data;
            this.timeTotal = data.result.total;
          }
        }
      });
    },
    // 增加消息
    addTime() {
      $("#adddTimeModal").modal("show");
    },
    // 删除消息
    deleteMessage(item, index) {
      $("#exampleModal").modal("show");
      this.index = index;
      this.closeTimeId = item.id;
    },
    confirmDelete() {
      $.ajax({
        type: "DELETE",
        url: window.url + "/admin/gymnastics/" + this.closeTimeId,
        success: data => {
          if (data.success) {
            $("#exampleModal").modal("hide");
            this.messageList.splice(this.index, 1);
            this.$alert("取消成功", "提示", {
              confirmButtonText: "确定",
               callback: action => {
                   this.getGymnastics()
               }
            });
          }else{
               this.$alert(data.msg, "提示", {
               confirmButtonText: "确定",
            });
          }
        }
      });
    },
    confirmAdd() {
      $.ajax({
        type: "POST",
        url: window.url + "/admin/gymnastics",
        data: {
          date: moment(this.orderDate).format("YYYY-MM-DD"),
          time: Number(this.radio)
        },
        success: data => {
          if (data.success) {
            console.log(data);
            $("#exampleModal").modal("hide");
            $("#adddTimeModal").modal("hide");
            this.getGymnastics()
            // this.messageList.splice(this.index, 1);
          }else{
               this.$alert(data.msg, "提示", {
               confirmButtonText: "确定",
            });
          }
        }
      });
    }
  }
};
</script>

<style>
.wrap-head {
  height: 80px;

  border-bottom: 1px solid #ccc;

  width: 100%;

  padding: 20px;
}

.message-wrap {
  width: 100%;

  padding: 40px 0px;

  margin-right: 0px;

  margin-left: 0px;
}
</style>




