import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/login/index'
import Home from '@/components/home/index'

// 消息路由
import Message from '@/components/message/index'
import addMessage from '@/components/message/add_message'

// 订单管理
import Order from '@/components/orders/index'
import HistoryOrder from '@/components/orders/history_order'
// 体操室管理
import Gymnastics from '@/components/orders/gymnastics'


// 闭馆时间管理
import CloseTime from '@/components/close_time/index'

// 管理员管理
import Users from '@/components/users/index'

// 统计信息
import TotalMessage from '@/components/total_message/index'



Vue.use(Router)

export default new Router({
  routes: [{
      path: '/',
      name: 'Login',
      component: Login
    },
    {
      path: '/home',
      component: Home,
      children: [{
        path: '/message',
        name: 'Message',
        component: Message
      },{
        path: '/add_message',
        name: 'addMessage',
        component: addMessage
      },{
        path: '/users',
        name: 'users',
        component: Users
      },{
        path: '/order',
        name: 'order',
        component: Order
      },{
        path: '/historyOrder',
        name: 'historyOrder',
        component: HistoryOrder
      },{
        path: '/closeTime',
        name: 'closeTime',
        component: CloseTime
      },{
        path: '/totalMessage',
        name: 'totalMessage',
        component: TotalMessage
      },{
        path: '/gymnastics',
        name: 'gymnastics',
        component: Gymnastics
      }]
    }
  ]
})



