<template>
    <div class="message">
        <div class="wrap-head">
            <div class="row">
                <div class="col-lg-6">
                    <div class="input-group">
                     <h4>统计信息</h4>
                    </div>
                </div>
                </div>
        </div>
        <div class="message-wrap row">
            <div class="col-md-12">
                <div class="panel panel-default">
                    <div class="table-responsive">
                        <div id="week-consume"></div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import Pagination from "../public/pagination";
export default {
  data() {
    return {
      dispalyExercise: true,
      dispalyConsume: false,
      xdata: [],
      ydata: [],
      errorMsg: "",
      errorTip: false
    };
  },
  mounted() {
    $.ajax({
      type: "GET",
      url: window.url + "/admin/sport",
      success: data => {
        if (data.success) {
          data.result.forEach(item => {
            let index = item.date.indexOf("-");
            let date = item.date.substring(index + 1);
            let obj = [date, item.value];
            this.xdata.push(obj);
          });
          this.xdata.reverse();
          this.drawExercise();
        }
      }
    });
  },
  methods: {
    weekExercise() {
      this.dispalyExercise = true;
      this.dispalyConsume = false;
      this.drawExercise();
    },
    drawExercise() {
      // 绘制图表
      let myChart = echarts.init(document.getElementById("week-consume"));
      var builderJson = {
        all: 1000,
        charts: {
          '1号': 323,
          '2号': 216,
          '3号': 751,
          '4号': 777,
          '5号': 735,
          '6号': 240,
          '7号': 184,
          '8号': 209,
          '9号': 176,
          '10号': 153,
          '11号': 206,
          '12号': 153
        },
        components: {
          '1号': 278,
          '2号': 955,
          '3号': 940,
          '4号': 946,
          '5号': 926,
          '6号': 341,
          '7号': 298,
          '8号': 273,
          '9号': 274,
          '10号': 246,
          '11号': 304,
          '12号': 195
        },
        ie: 974
      };

      var downloadJson = {
           '1号': 323,
          '2号': 216,
          '3号': 751,
          '4号': 777,
          '5号': 735,
          '6号': 240,
          '7号': 184,
          '8号': 209,
          '9号': 176,
          '10号': 153,
          '11号': 206,
          '12号': 153
      };

      var themeJson = {
          '1号': 278,
          '2号': 955,
          '3号': 940,
          '4号': 946,
          '5号': 926,
          '6号': 341,
          '7号': 298,
          '8号': 273,
          '9号': 274,
          '10号': 246,
          '11号': 304,
          '12号': 195
      };

      var waterMarkText = "";

      var canvas = document.createElement("canvas");
      var ctx = canvas.getContext("2d");
      canvas.width = canvas.height = 100;
      ctx.textAlign = "center";
      ctx.textBaseline = "middle";
      ctx.globalAlpha = 0.08;
      ctx.font = "20px Microsoft Yahei";
      ctx.translate(50, 50);
      ctx.rotate(-Math.PI / 4);
      ctx.fillText(waterMarkText, 0, 0);

      var option = {
        backgroundColor: {
          type: "pattern",
          image: canvas,
          repeat: "repeat"
        },
        tooltip: {},
        title: [
          {
            text: "场地近一周预订时常",
            // subtext: "总计 " + builderJson.all,
            x: "25%",
            textAlign: "center"
          },
          {
            text: "场地近一周消费总额",
            // subtext: "总计 " + builderJson.all,
            x: "25%",
            y: "53%",
            textAlign: "center"
          },
          {
            text: "场地近一周预订时常",
            x: "75%",
            textAlign: "center"
          },
          {
            text: "场地近一周消费总额",
            x: "75%",
            y: "50%",
            textAlign: "center"
          }
        ],
        grid: [
          {
            top: 50,
            width: "50%",
            bottom: "50%",
            left: "1%",
            containLabel: true
          },
          {
            top: "60%",
            width: "50%",
            bottom: 0,
            left:"1%",
            containLabel: true
          },
            {
            top: 50,
            width: "50%",
            bottom: "50%",
            right: "1%",
            containLabel: true
          },
          {
            top: "60%",
            width: "50%",
            bottom: 0,
            right:"1%",
            containLabel: true
          }
        ],
        xAxis: [
          {
            type: "value",
            max: builderJson.all,
            splitLine: {
              show: false
            }
          },
          {
            type: "value",
            max: builderJson.all,
            gridIndex: 1,
            splitLine: {
              show: false
            }
          }
        ],
        yAxis: [
          {
            type: "category",
            data: Object.keys(builderJson.charts),
            axisLabel: {
              interval: 0,
              rotate: 30
            },
            splitLine: {
              show: false
            }
          },
          {
            gridIndex: 1,
            type: "category",
            data: Object.keys(builderJson.components),
            axisLabel: {
              interval: 0,
              rotate: 30
            },
            splitLine: {
              show: false
            }
          }
        ],
        series: [
          {
            type: "bar",
            stack: "chart",
            z: 3,
            label: {
              normal: {
                position: "right",
                show: true
              }
            },
            data: Object.keys(builderJson.charts).map(function(key) {
              return builderJson.charts[key];
            })
          },
          {
            type: "bar",
            stack: "chart",
            silent: true,
            itemStyle: {
              normal: {
                color: "#eee"
              }
            },
            data: Object.keys(builderJson.charts).map(function(key) {
              return builderJson.all - builderJson.charts[key];
            })
          },
          {
            type: "bar",
            stack: "component",
            xAxisIndex: 1,
            yAxisIndex: 1,
            z: 3,
            label: {
              normal: {
                position: "right",
                show: true
              }
            },
            data: Object.keys(builderJson.components).map(function(key) {
              return builderJson.components[key];
            })
          },
          {
            type: "bar",
            stack: "component",
            silent: true,
            xAxisIndex: 1,
            yAxisIndex: 1,
            itemStyle: {
              normal: {
                color: "#eee"
              }
            },
            data: Object.keys(builderJson.components).map(function(key) {
              return builderJson.all - builderJson.components[key];
            })
          },
          {
            type: "pie",
            radius: [0, "30%"],
            center: ["75%", "25%"],
            data: Object.keys(downloadJson).map(function(key) {
              return {
                name: key.replace(".js", ""),
                value: downloadJson[key]
              };
            })
          },
          {
            type: "pie",
            radius: [0, "30%"],
            center: ["75%", "75%"],
            data: Object.keys(themeJson).map(function(key) {
              return {
                name: key.replace(".js", ""),
                value: themeJson[key]
              };
            })
          }
        ]
      };
      myChart.setOption(option);
    }
  }
};
</script>

<style>
.message{
   overflow: hidden;
}
#week-consume {
  margin: 40px auto;
  /* width: 94%;
        height: 40%; */
  width: 960px;
  height: 700px;
  padding: 0px 10px;
 
}
.wrap-head {
  height: 80px;
  border-bottom: 1px solid #ccc;
  width: 100%;
  padding: 20px;
}
</style>



