<template>
  <div class="users">
       <div class="wrap-head">
            <button type="button" class="btn btn-success" @click="addUsers">添加管理员</button>
        </div>
        <div class="message-wrap row">
            <div class="col-md-12">
                <div class="panel panel-default">
                    <div class="table-responsive">
                        <table class="all-table table table-hover table-bordered">
                            <thead>
                                <tr>
                                    <th>#</th>
                                    <th>管理员工号</th>
                                    <th>操作</th>
                                 </tr>
                            </thead>
                            <tbody>
                                <tr v-for="(item,index) in usersList" :key="item.uid">
                                    <td>{{index+1}}</td>
                                    <td>{{item}}</td>
                                    <td>
                                        <button type="button" class="btn btn-danger"  @click="deleteMessage(item,index)">删除</button>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        </div>
        <div class="modal" id="exampleModal" tabindex="-1" role="dialog"  aria-labelledby="exampleModalCenterTitle" aria-hidden="true">
            <div class="modal-dialog modal-dialog-centered" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">删除</h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                            </button>
                    </div>
                    <div class="modal-body">
                        <p>确认删除 "{{userId}}" 管理员?</p>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-primary" @click="confirmDelete">确定</button>
                        <button type="button" class="btn btn-secondary" data-dismiss="modal">取消</button>
                    </div>
                </div>
            </div>
        </div>
        <div class="modal" id="adddUserModal" tabindex="-1" role="dialog"  aria-labelledby="exampleModalCenterTitle" aria-hidden="true">
            <div class="modal-dialog modal-dialog-centered" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">添加管理员</h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                            </button>
                    </div>
                    <div class="modal-body">
                        <div class="input-group">
                            <span class="input-group-addon" id="sizing-addon2">管理员工号</span>
                            <input type="text" class="form-control"  aria-describedby="sizing-addon2" v-model="addUserId" @blur="checkID">
                        </div>
                        <div class="check-error" v-if="showErrorTip">*请输入10位正确的学号</div>
                        <div class="alert alert-warning alert-dismissible" role="alert" style="margin-top:4px;" v-if="showWarning">
                                <button type="button" class="close" data-dismiss="alert" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                                请输入工号
                        </div>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-primary" @click="confirmAdd">确定</button>
                        <button type="button" class="btn btn-secondary" data-dismiss="modal">取消</button>
                    </div>
                </div>
            </div>
        </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      usersList: [],
      userId: 0,
      index: 0,
      addUserId: "",
      showWarning: false,
      showErrorTip: false
    };
  },

  mounted() {
    $.ajax({
      type: "GET",
      url: window.url + "/root/admin/all",
      success: data => {
        if (data.success) {
          this.usersList = data.result;
        }
      }
    });
  },

  methods: {
    // 校验用户ID
    checkID() {
      let reg = /^\d{10}$/;
      if (this.addUserId) {
        if (reg.test(this.addUserId)) {
          this.showErrorTip = false;
        } else {
          this.showErrorTip = true;
        }
      } else {
        this.showErrorTip = false;
      }
    },
    getAllUser() {
      $.ajax({
        type: "GET",
        url: window.url + "/root/admin/all",
        success: data => {
          if (data.success) {
            this.usersList = data.result;
          }
        }
      });
    },
    // 增加推送
    addUsers() {
      $("#adddUserModal").modal("show");
    },
    // 删除推送
    deleteMessage(item, index) {
      this.userId = item;
      $("#exampleModal").modal("show");
      this.index = index;
    },
    confirmDelete() {
      $.ajax({
        type: "DELETE",
        url: window.url + "/root/admin/" + this.userId,
        success: data => {
          if (data.success) {
            $("#exampleModal").modal("hide");
            this.usersList.splice(this.index, 1);
          }
        }
      });
    },
    confirmAdd() {
      if (this.addUserId) {
        $.ajax({
          type: "PUT",
          url: window.url + "/root/admin/" + this.addUserId,
          success: data => {
            if (data.success) {
              this.addUserId = "";
              this.getAllUser()
              $("#exampleModal").modal("hide");
            }
          }
        });
        $("#adddUserModal").modal("hide");
      } else {
        this.showWarning = true;
      }
    }
  }
};
</script>

<style>
.wrap-head {
  height: 80px;
  border-bottom: 1px solid #ccc;
  width: 100%;
  padding: 20px;
}

.message-wrap {
  width: 100%;
  padding: 40px 0px;
  margin-right: 0px;
  margin-left: 0px;
}
.check-error {
  color: #d9534f;
  font-size: 10px;
}
</style>




