<template>
    <div class="message">
        <div class="wrap-head">
            <div class="row">
                <div class="col-lg-6">
                    <div class="input-group">
                    <input type="num" class="form-control" placeholder="输入用户学号" v-model="searchUserId" @blur="checkID">
                    <span class="input-group-btn">
                        <button class="btn btn-default" type="button" @click="searchOrder">搜索</button>
                    </span>
                    </div>
                    <div class="check-error" v-if="showErrorTip">*请输入10位正确的学号</div>
                </div>
                </div>
        </div>
        <div class="message-wrap row">
            <div class="col-md-12">
                <div class="panel panel-default">
                    <div class="table-responsive">
                        <table class="all-table table table-hover table-bordered">
                            <thead>
                                <tr>
                                    <th>ID</th>
                                    <th>预订用户</th>
                                    <th>预订时间</th>
                                    <th>预订场地</th>
                                    <th>操作</th>
                                 </tr>
                            </thead>
                            <tbody>
                                <tr v-for="(item,index) in orderList" :key="item.id">
                                    <td>{{item.id}}</td>
                                    <td>{{item.uid}}</td>
                                    <td>{{item.startTime|time}}-{{item.endTime|time}}</td>
                                    <td>{{item.gid}}</td>
                                    <td>
                                        <button type="button" class="btn btn-primary"  @click="payOrder(item,index)">支付</button>
                                        <button type="button" class="btn btn-danger"  @click="deleteOrder(item,index)">取消</button>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        </div>
         <pagination :total="total" @pagination="pagination" v-if="total > 10"></pagination>
        <div class="modal" id="exampleModal" tabindex="-1" role="dialog"  aria-labelledby="exampleModalCenterTitle" aria-hidden="true">
            <div class="modal-dialog modal-dialog-centered" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">提示</h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                            </button>
                    </div>
                    <div class="modal-body">
                        <p>{{confirmTip}}</p>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-primary" @click="confirmDelete" v-if="showDelete">确定</button>
                        <button type="button" class="btn btn-primary" @click="confrimPay" v-else>确定</button>
                        <button type="button" class="btn btn-secondary" data-dismiss="modal">取消</button>
                    </div>
                </div>
            </div>
        </div>
        <div class="modal" id="successModal" tabindex="-1" role="dialog"  aria-labelledby="exampleModalCenterTitle" aria-hidden="true">
            <div class="modal-dialog modal-dialog-centered" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">提示</h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                            </button>
                    </div>
                    <div class="modal-body">
                        <p>{{optionTip}}</p>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-primary" @click="confirmOption">确定</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import Pagination from "../public/pagination";
export default {
  data() {
    return {
      searchUserId: "",
      orderList: [],
      showDelete: false, // 显示删除还是支付按钮
      userId: 0, // 用户学号
      orderId: 0, //订单ID
      index: 0,
      total: 0,
      confirmTip: "", //取消订单提示
      optionTip: "操作成功！", // 操作提示
      showErrorTip: false // 校验用户学号
    };
  },
  components: {
    Pagination
  },
  mounted() {
    // 获取未支付的订单
    this.getAllOrders();
  },

  methods: {
    // 获取未支付的订单
    getAllOrders() {
      $.ajax({
        type: "GET",
        url: window.url + "/admin/order/notpaid",
        success: data => {
          if (data.success) {
            console.log(data.result);
            this.orderList = data.result.data;
            this.total = data.result.total;
          }
        }
      });
    },
    // 分页
    pagination(page) {
      $.ajax({
        type: "GET",
        url: window.url + "/admin/order/notpaid?page=" + page,
        success: data => {
          if (data.success) {
            this.orderList = data.result.data;
            this.total = data.result.total;
          }
        }
      });
    },
    // 校验用户ID
    checkID() {
      let reg = /^\d{10}$/;
      if (this.searchUserId) {
        if (reg.test(this.searchUserId)) {
          this.showErrorTip = false;
        } else {
          this.showErrorTip = true;
        }
      } else {
        this.getAllOrders();
        this.showErrorTip = false;
      }
    },
    // 搜索
    searchOrder() {
      if (this.searchUserId) {
        $.ajax({
          type: "GET",
          url: window.url + "/admin/order/search?uid=" + this.searchUserId,
          success: data => {
            if (data.success) {
              this.orderList = data.result.data;
            }
          }
        });
      }
    },
    // 支付订单
    payOrder(item, index) {
      this.userId = item.uid; // 用户学号
      this.orderId = item.id;
      this.confirmTip = "确认学号为" + this.userId + "的订单已支付?";
      $("#exampleModal").modal("show");
      this.index = index;
      this.showDelete = false;
    },
    // 确认支付
    confrimPay() {
      $.ajax({
        type: "PUT",
        url: window.url + "/admin/payment/" + this.orderId,
        success: data => {
          if (data.success) {
            $("#exampleModal").modal("hide");
            this.orderList.splice(this.index, 1);
            // 提示操作成功
            $("#successModal").modal("show");
          } else {
            this.optionTip = data.msg; // 提示操作出错
            $("#successModal").modal("show");
          }
        }
      });
    },
    // 取消订单
    deleteOrder(item, index) {
      this.userId = item.uid; // 用户学号
      this.orderId = item.id;
      this.confirmTip = "确认取消学号为" + this.userId + "的订单?";
      $("#exampleModal").modal("show");
      this.index = index;
      this.showDelete = true;
    },
    confirmDelete() {
      $.ajax({
        type: "PUT",
        url: window.url + "/order/cancel",
        data: {
          orderId: this.orderId
        },
        success: data => {
          if (data.success) {
            $("#exampleModal").modal("hide");
            this.orderList.splice(this.index, 1);
            // 提示操作成功
            $("#successModal").modal("show");
          } else {
            this.optionTip = data.msg; // 提示操作出错
            $("#successModal").modal("show");
          }
        }
      });
    },
    // 确认提示信息
    confirmOption() {
      this.getAllOrders();
      $("#successModal").modal("hide");
    }
  }
};
</script>

<style>
.wrap-head {
  height: 80px;

  border-bottom: 1px solid #ccc;

  width: 100%;

  padding: 20px;
}

.message-wrap {
  width: 100%;

  padding: 40px 0px;

  margin-right: 0px;

  margin-left: 0px;
}
.check-error {
  color: #d9534f;
  font-size: 10px;
}
</style>




