<template>
    <div class="message">
        <div class="wrap-head">
            <div class="row">
                <div class="col-lg-6">
                    <div class="input-group">
                     <h4>历史订单</h4>
                    </div>
                </div>
                </div>
        </div>
        <div class="message-wrap row">
            <div class="col-md-12">
                <div class="panel panel-default">
                    <div class="table-responsive">
                        <table class="all-table table table-hover table-bordered">
                            <thead>
                                <tr>
                                    <th>ID</th>
                                    <th>预订用户</th>
                                    <th>预订时间</th>
                                    <th>预订场地</th>
                                 </tr>
                            </thead>
                            <tbody>
                                <tr v-for="item in orderList" :key="item.id">
                                    <td>{{item.id}}</td>
                                    <td>{{item.uid}}</td>
                                    <td>{{item.startTime|time}}-{{item.endTime|time}}</td>
                                    <td>{{item.gid}}</td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        </div>
         <pagination :total="total" @pagination="pagination" v-if="total > 10"></pagination>
    </div>
</template>
<script>
import Pagination from "../public/pagination";
export default {
  data() {
    return {
      orderList: [],
      total: 0
    };
  },
  components: {
    Pagination
  },
  mounted() {
    // 获取历史的订单
    this.getAllOrders();
  },

  methods: {
    // 获取历史的订单
    getAllOrders() {
      $.ajax({
        type: "GET",
        url: window.url + "/admin/order/paid",
        success: data => {
          if (data.success) {
            console.log(data.result);
            this.orderList = data.result.data;
            this.total = data.result.total;
          }
        }
      });
    },
    // 分页
    pagination(page) {
      $.ajax({
        type: "GET",
        url: window.url + "/admin/order/paid?page=" + page,
        success: data => {
          if (data.success) {
            this.orderList = data.result.data;
            this.total = data.result.total;
          }
        }
      });
    },
  }
};
</script>

<style>
.wrap-head {
  height: 80px;

  border-bottom: 1px solid #ccc;

  width: 100%;

  padding: 20px;
}

.message-wrap {
  width: 100%;

  padding: 40px 0px;

  margin-right: 0px;

  margin-left: 0px;
}
.check-error {
  color: #d9534f;
  font-size: 10px;
}
</style>




