<template>
    <nav aria-label="Page navigation" style="text-align: center;">
        <ul class="pagination">
            <li :class="{'disabled':current===1}">
                <a href="javascript:void(0)" aria-label="Previous" @click="prePage">
                    <span aria-hidden="true">&laquo;</span>
                </a>
            </li>
            <li :class="{'active': current === item}" v-for="(item,index) in pages" :key="item" @click="linkPage(item,index)"><a href="javascript:void(0)" >{{item}}</a></li>
            <li :class="{'disabled':current === sum}">
                <a href="javascript:void(0)" aria-label="Next" @click="nextPage">
                    <span aria-hidden="true">&raquo;</span>
                </a>
            </li>
        </ul>
    </nav>
</template>
<script>
export default {
  props: {
    total: Number
  },
  data() {
    return {
      current: 1,
      sum: 0
    };
  },
  computed: {
    pages() {
      this.sum = Math.ceil(this.total / 10);
      return this.sum;
    }
  },
  methods: {
    prePage() {
      if (this.current > 1) {
        this.$emit("pagination", this.current - 1);
        this.current = this.current - 1;
      }
    },
    linkPage(item, index) {
      this.current = item;
      this.$emit("pagination", item);
    },
    nextPage() {
      if (this.current < this.sum) {
        this.$emit("pagination", this.current + 1);
        this.current = this.current + 1;
      }
    }
  }
};
</script>
<style>
.current-active {
  color: #fff;
  cursor: default;
  background-color: #337ab7;
  border-color: #337ab7;
}
</style>




